<template>
  <div class="staticReports">
    <el-container>
      <el-header>
        <div class="staticReports loadingtext minWidth"
            v-loading="imgLoading"
            element-loading-text="加载中..."
            element-loading-background="rgba(255, 255, 255, 0.8)">
          <!-- 头部面包屑 begin -->
          <!--<el-row class="staticReports-header">
            <el-col :span="24">
              <span class="mz" @click="$router.back(-1)">流程报表</span> > <span class="fz16">静态-流程静态配置导出</span>
            </el-col>
	  </el-row>-->
          <div class="taticReports-content">
	        <el-row type="flex" class="staticReports-header" justify="space-between">
          	  <el-col :span="17" class="titleMT"><span class="mz" @click="$router.back(-1)">流程报表</span><i :class="icon"></i>
                </el-col>
                <el-col :span="6" class="titleBtn  clearfix">
                    <el-button round @click="reset" type="primary"  size="mini">重置</el-button>
                    <el-button round @click="search" type="primary" size="mini">搜索</el-button>
                </el-col>
          </el-row>
          <br>
          <!--<hr>-->
          <div class="statement reportCondition">
            <span class="fz16">静态-流程静态配置导出</span>
            <el-row class="staticReports-middle lh35 staticReportsMiddle">
                  <el-col :span="2" :sm="4" :md="1" class="classSelect"><span>流程分类选择</span></el-col>
                  <el-col :span="7" class="classSelectMR process-inputbox">
                    <i class="iconfont icon-organization"  @click="openDailog('processClassification')"></i>
                    <span @dblclick="openDailog('processClassification')">
                  <el-input  disabled v-model="processCategoryName"    class="input-text"  :title="processCategoryName"></el-input>
                  </span>
                  </el-col>
                  <el-col :span="1"><span>&nbsp;</span></el-col>
                  <el-col :span="2" :sm="4" :md="1" class="processSelect"><span>流程选择</span></el-col>
                  <el-col :span="6" class="process-inputbox">
                   <i class="iconfont icon-organization" @click="openDailog('processSelection')"></i>
                   <span @dblclick="openDailog('processSelection')">
                    <el-input  disabled  class="input-text"  v-model="processName"  :title="processName" ></el-input>
                  </span>
                  </el-col>
            </el-row>

            <el-row class="reportCondition-date reportConditionDate">
                <el-col :span="4"  class="buttonWrap">
                  <!--<el-button @click="search" type="primary"  class="upload" size="mini" icon=" el-icon-search" >查询</el-button>
                  <el-button @click="reset" type="primary" class="upload" size="mini" icon=" el-icon-refresh" >重置</el-button>-->
                  <el-button round @click="exportList" class="upload" type="primary" icon="iconfont icon-export" size="mini">导出</el-button>
                </el-col>
            </el-row>
          </div>
          <!-- 报表结果 begin -->
              <div class="statement ">
                <el-table
                  ref="singleTable"
                  :data="tableData"
                  highlight-current-row
                  :height="isShow?fullHeight-275:fullHeight-315"
                  border
                  @sort-change="sortChange"
                  class="table"
                  >
                  <el-table-column
                    prop="orgName"
                    label="公司"
                    width="120px"
                    sortable="custom"
                    align="center">
                  </el-table-column>

                  <el-table-column
                    prop="processFullName"
                    label="目录"
                     min-width="102px"
                     sortable="custom"
                    align="center">
                  </el-table-column>

                  <el-table-column
                    prop="processID"
                    label="ID"
                    width="60px"
                    sortable="custom"
                    align="center">
                  </el-table-column>

                  <el-table-column
                    prop="processname"
                    label="流程名称"
                    width="140px"
                    sortable="custom"
                    align="left">
                    <template slot-scope="scope">
                      <div class="table-popover" :title="scope.row.processName">{{ scope.row.processName }}</div>
                    </template>
                  </el-table-column>

                  <el-table-column
                    prop="templeName"
                    label="模板名称"
                    width="124px"
                    sortable="custom"
                    align="center">
                  </el-table-column>

                  <el-table-column
                    prop="stepInfos"
                    label="步骤信息"
                    sortable="custom"
                    min-width="180px"
                    >
                  </el-table-column>

                  <el-table-column
                    prop="processCCUser"
                    label="抄送人"
                    width="140px"
                    sortable="custom"
                    align="center">
                  </el-table-column>

                  <el-table-column
                    prop="isAvailable"
                    label="是否有效"
                    width="98px"
                    sortable="custom"
                    align="center">
                  </el-table-column>
                  <div slot="empty" v-show="!imgLoading">
                    <p :style="{'marginTop': '23px'}">暂无数据</p>
                  </div>
                  <div slot="append"> 
                    <!--分页-->
                    <page :pageIndex="pageIndex" :total="total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>        
                  </div>
                </el-table>
              </div>
            
          </div>
          <!-- 报表结果 end -->

        </div>

        <!--弹窗组件-->
         <component :is="componentParam.componentType" v-if="showdialog"
                   @hideComponent="hideComponent" v-on:sureSubmit="showMsgfromchild"
                   v-bind:componentParam="componentParam"></component>
      </el-header>
    </el-container>
  </div>
</template>


<script>
  import util from '../js/util'
  import processClassification from '../components/dailog/processClassification.vue'
  import processSelection from '../components/dailog/processSelection.vue'
  import loading from '../plugins/loading/loading';
  import page from "../plugins/page/index";

  export default {
    name: 'staticReports',
    /**
     * 列表界面构造函数
     */
     mounted() {
    },
    components:{
      'page':page,
      'processClassification': processClassification, //选择流程分类
      'processSelection':processSelection             //选择流程
    },
    data() {
       return {
        processCategoryName:'',//流程分类名
        processCategoryIDs:'',//流程分类id
        processCategoryList:[],//流程分类选中数据
        processName:'',// 流程名
        processIDs:'',//流程id
        processList:[],//流程选中数据

       componentParam: {
          //弹窗类型
          componentType:{
            type: String,
            required: true
          },
          //需要 回填到组件上的 数据集合
          backfillData:{
            type: Array,
            required: true
          },
          //子组件是否单选(true单选，false不是单选)
          oneCheck:{
            type: Boolean,
            required: true
          },
          //控制弹窗头部标题显示隐藏(默认显示)
          closeShow:{
            type: Boolean,
            required: true
          },
        },
        showdialog: false,//控制弹窗打开关闭
         fullHeight:document.documentElement.clientHeight,
         // 查询条件
        model : {
         selectForm:{},
         companys:''
        },
        isShow:true,// 报表结果标题内容是否展开收起
        arrt:'收起',
        icon:'el-icon-arrow-down',
        selectValue: '',
        orgId:'',
        orderBy:'',
        tableData: [],// table数据
        showdialog:false,
        showList: false, //列表显示状态
        exportData:[],
        // 分页参数
        pageIndex:1,//当前分页的索引
        total:0,//数据总条数
        pageSize:10, //当前分页显示的条数
        pageSizesList:[10,20,50,100],//分页按钮
        // 加载动画
        imgLoading: false
      }
    },
    methods:{
        /**
       * 查询按钮
       */
      search() {
          this.pageIndex= 1;
        if(this.processCategoryName.length == 0 && this.processName.length  == 0){
           this.$message.warning("请选择流程分类或流程选择进行查询！");
        }else{
          this.getList();
        }
      },
      /**
       * 表格排序
       */
      sortChange(column){
        if(this.processCategoryName.length !== 0 || this.processName.length !== 0){
          this.orderBy = `${column.prop} ${column.order.replace('ending','')}`;
          this.getList();
        }
      },

        openDailog(type) {
        //控制弹窗头部标题显示隐藏(默认显示)
        this.componentParam.closeShow = false;
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = false;
        //设置回填数据
        if(type == 'processClassification'){
          this.componentParam.backfillData = this.processCategoryList;
        }else  if(type == 'processSelection'){
          this.componentParam.backfillData = this.processList;
        }
        this.showdialog = true;
        util.showDailog_mask();//解决ie浏览器问题显示遮罩
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        //流程分类 选择
        if (data.type && data.type == "processClassification") {
          this.processCategoryList= data.list;
          let  processCategoryIDs = [];
          let  processCategoryName = [];
          for(let node of data.list){
            processCategoryName.push(node.name);
            processCategoryIDs.push(node.id+'');
          }
          this.processCategoryIDs = processCategoryIDs.join(',');
          this.processCategoryName = processCategoryName.join('；');
        }else if(data.type && data.type == "processSelection"){
          //流程 选择
          this.processList = data.list;
          let processId = [];
          let processName = [];
          for(let node of data.list){
            processName.push(node.name);
            processId.push(node.processId+'');
          }
          this.processIDs = processId.join(',');
          this.processName = processName.join('；');
        }
        this.hideComponent();
      },
      //获取所有 processId
      getProcessID(nodes,process){
        for(let node of nodes){
          if(node.Child){
            this.getProcessID(node.Child,process);
            }else{
              if(node.processId && node.processId!=='0' && node.processId!==null){
                  process.processId.push(node.processId+'');
              }else{
                process.processCategoryIDs.push(node.id+'');
              }
               return;
          }
          process.processName.push(node.name.trim());
        }
      },
      /**
       * 重置按钮
       */
      reset(){
        this.componentParam.backfillData = new Array();
        this.processList = new Array();
        this.processCategoryList = new Array();
        this.$nextTick(() => {
          this.model.companys = '';
          this.model.selectForm.departmentID = '';
          this.processName = '';//流程类别
          this.processIDs='';//流程ID
          this.processCategoryName = '';//流程分类名
          this.processCategoryIDs='';//流程分类id
        })
      },
      /**
       * 获取列表数据
       */
      getList(){
        var that=this;
        // loading.startLoading();
        this.imgLoading = true;
        let data = {
          poisitionID :null,
          isAvailable:"",
          processIDs:this.processIDs,
          processCategoryIDs:this.processCategoryIDs,
          orgID:that.model.selectForm.departmentID,
          pageIndex:that.pageIndex-1,
          pageSize:that.pageSize,
          orderBy:that.orderBy //排序
        };

        that.$axios.post('/api/reportForm/positionApprovedProcesses',
         data).then( (result) => {
          that.tableData = result.data.processList;
          that.total = result.data.listCounts;

        for (let i = 0; i < result.data.processList.length; i++) {
            switch (Number(result.data.processList[i].isAvailable)) {
              case 0:
                result.data.processList[i].isAvailable = '否';
                break;
              case 1:
                result.data.processList[i].isAvailable = '是';
                break;
              default:
                result.data.processList[i].isAvailable = '否';
            }
          }

          // loading.endLoading();
          this.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading();
          this.imgLoading = false;
          that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        })
      },
      /**
       * 点击收起展开
       */
      showToggle(){
        this.isShow=!this.isShow;
        if(this.isShow){
          this.arrt="收起"
          this.icon="el-icon-arrow-up"
        }else {
          this.arrt="展开"
          this.icon="el-icon-arrow-down"
        }
      },
      /**
       * 导出
       */
      exportList() {
        let that = this;
         loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        let obj={
          "tableName": "静态流程静态配置导出",
          params:{
            "processCategoryIDs":that.processCategoryIDs,
            "processIDs":that.processIDs,
            "pageIndex":that.pageIndex-1,
            "pageSize":that.total,
            "orderBy": that.orderBy,
            "userID":"",
            "orgID":'',
          },
        };
          this.$axios.post('/api/exportExcelForTable', obj, {responseType: 'blob'}).then((result) => {
          const content = result.data;
          const dowLoadFileName = '静态-流程静态配置导出';
          util.exportSearchList(dowLoadFileName, content)
          loading.endLoading();
        }).catch((err) => {
          loading.endLoading();
          this.$message.error(`导出失败!ERROR MESSAGE:${err}`);
        })
      },
      /**
       * 页面size变更方法
       */
      handleSizeChange(val) {
        var likeThis = this;
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        this.getList();
      },
      /**
       * 翻页方法
       */
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getList();
      },
      /**
       *设置表头标题class
       */
      tableHeaderClassName({ row , rowIndex }) {
        return "table-head-th";
      },

    },
      computed: {},
      watch: {
      fullHeight () {
        return document.documentElement.clientHeight;
      }
    }
  }
</script>

<style lang="scss" scoped>
</style>



